import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { StatusEnum } from '../../core/enums/status.enum';
import { TransactionExtendedDto } from '../transaction-extended-dto';
import { TransactionsService } from '../transactions.service';

@Component({
  selector: 'tbtl-transaction',
  templateUrl: './transaction.component.html',
  styleUrls: ['./transaction.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionComponent implements OnInit {
  transaction$: Observable<TransactionExtendedDto>;
  StatusEnum = StatusEnum;

  constructor(private route: ActivatedRoute, private snackBar: MatSnackBar, private transactionsService: TransactionsService) {}

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    const transactionId = this.route.snapshot.params.transactionId;
    this.transaction$ = this.transactionsService.getTransactionById(+transactionId);
  }

  getStatusColor(verificationStatus: StatusEnum) {
    return verificationStatus === StatusEnum.Pending ? 'pending' : verificationStatus === StatusEnum.Completed ? 'accent' : 'warn';
  }

  hasTagCopied() {
    this.snackBar.open('Transaction Hash has been copied to clipboard', '', {
      duration: 2000,
    });
  }
}
