import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { TransactionDto } from '../../transactions/transaction-dto';
import { TransactionTypeEnum } from '../../transactions/transaction-type.enum';
import { WalletDto } from '../wallet-dto';

@Component({
  selector: 'tbtl-wallet-transaction-table',
  templateUrl: './wallet-transaction-table.component.html',
  styleUrls: ['./wallet-transaction-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class WalletTransactionTableComponent implements OnChanges {
  @Input() transactionList: TransactionDto[] = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns = ['hash', 'type', 'walletAddress', 'amount', 'date', 'actions'];
  dataSource: MatTableDataSource<TransactionDto>;

  constructor() {}

  ngOnChanges() {
    this.dataSource = new MatTableDataSource<TransactionDto>(this.transactionList);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  transactionTypeColor(transaction: TransactionDto) {
    return transaction.type === TransactionTypeEnum.SENDING ? 'accent' : 'warn';
  }

  transactionType(transaction: TransactionDto): WalletDto {
    return transaction.type === TransactionTypeEnum.SENDING ? transaction.toWallet : transaction.fromWallet;
  }
}
