import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { TransactionDto } from '../transaction-dto';

@Component({
  selector: 'tbtl-transactions-table',
  templateUrl: './transactions-table.component.html',
  styleUrls: ['./transactions-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsTableComponent implements OnChanges {
  @Input() transactionData: TransactionDto[] = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns = ['hash', 'fromWallet', 'toWallet', 'amount', 'date', 'actions'];
  dataSource: MatTableDataSource<TransactionDto>;

  constructor() {}

  ngOnChanges() {
    this.dataSource = new MatTableDataSource<TransactionDto>(this.transactionData);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
