import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { WalletDto } from '../wallet-dto';

@Component({
  selector: 'tbtl-wallets-table',
  templateUrl: './wallets-table.component.html',
  styleUrls: ['./wallets-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class WalletsTableComponent implements OnChanges {
  @Input() walletData: WalletDto[] = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns = ['name', 'address', 'type', 'totalTransactions', 'amount', 'actions'];
  dataSource: MatTableDataSource<WalletDto>;

  constructor() {}

  ngOnChanges() {
    this.dataSource = new MatTableDataSource<WalletDto>(this.walletData);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
