import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { WalletDto } from './wallet-dto';
import { WalletsService } from './wallets.service';

@Component({
  selector: 'tbtl-wallets',
  templateUrl: './wallets.component.html',
  styleUrls: ['./wallets.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class WalletsComponent implements OnInit {
  walletsList: WalletDto[];
  filteredWalletsList: WalletDto[];

  constructor(private walletsService: WalletsService, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.walletsService.getWallets().subscribe((wallets) => {
      this.walletsList = wallets;
      this.filteredWalletsList = wallets;
      this.changeDetectorRef.markForCheck();
    });
  }

  // Filtering by wallet name
  filterWallets(event: KeyboardEvent) {
    const filter = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredWalletsList = this.walletsList.filter((wallet: WalletDto) => wallet.name.toLowerCase().includes(filter));
  }
}
