import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { IdentityDto } from '../identity-dto';

@Component({
  selector: 'tbtl-identities-table',
  templateUrl: './identities-table.component.html',
  styleUrls: ['./identities-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IdentitiesTableComponent implements OnChanges {
  @Input() identitiesData: IdentityDto[] = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns = ['name', 'country', 'wallets', 'totalTransactions', 'actions'];
  dataSource: MatTableDataSource<IdentityDto>;

  constructor() {}

  ngOnChanges() {
    this.dataSource = new MatTableDataSource<IdentityDto>(this.identitiesData);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
