import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { combineLatest, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { WalletDto } from '../../wallets/wallet-dto';
import { IdentitiesService } from '../identities.service';
import { IdentityExtendedDto } from '../identity-extended-dto';

@Component({
  selector: 'tbtl-identity',
  templateUrl: './identity.component.html',
  styleUrls: ['./identity.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IdentityComponent implements OnInit, OnDestroy {
  private unsubscribe$ = new Subject();

  identity: IdentityExtendedDto;
  walletList: WalletDto[];

  constructor(private route: ActivatedRoute, private identitiesService: IdentitiesService, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.loadData();
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  loadData() {
    const identityId = this.route.snapshot.params.identityId;

    combineLatest([this.identitiesService.getIdentityById(+identityId), this.identitiesService.getWalletsByIdentityId(+identityId)])
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(([identity, walletList]: [IdentityExtendedDto, WalletDto[]]) => {
        this.identity = identity;
        this.walletList = walletList;
        this.changeDetectorRef.markForCheck();
      });
  }
}
