import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { WalletDto } from '../../wallets/wallet-dto';

@Component({
  selector: 'tbtl-identity-wallet-table',
  templateUrl: './identity-wallet-table.component.html',
  styleUrls: ['./identity-wallet-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IdentityWalletTableComponent implements OnChanges {
  @Input() walletList: WalletDto[] = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns = ['name', 'address', 'role', 'totalTransactions', 'amount', 'actions'];
  dataSource: MatTableDataSource<WalletDto>;

  constructor() {}

  ngOnChanges() {
    this.dataSource = new MatTableDataSource<WalletDto>(this.walletList);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
