import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { TransactionDto } from './transaction-dto';
import { TransactionsService } from './transactions.service';

@Component({
  selector: 'tbtl-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsComponent implements OnInit {
  transactionsList: TransactionDto[];
  filteredTransactionsList: TransactionDto[];

  constructor(private transactionsService: TransactionsService, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.transactionsService.getTransactions().subscribe((transactions) => {
      this.transactionsList = transactions;
      this.filteredTransactionsList = transactions;
      this.changeDetectorRef.markForCheck();
    });
  }

  // Filtering by transaction Hash value
  filterTransactions(event: KeyboardEvent) {
    const filter = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredTransactionsList = this.transactionsList.filter((trans: TransactionDto) => trans.hash.toLowerCase().includes(filter));
  }
}
