import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { IdentitiesService } from './identities.service';
import { IdentityDto } from './identity-dto';

@Component({
  selector: 'tbtl-identities',
  templateUrl: './identities.component.html',
  styleUrls: ['./identities.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IdentitiesComponent implements OnInit {
  identitiesList: IdentityDto[];
  filteredIdentitiesList: IdentityDto[];

  constructor(private identitiesService: IdentitiesService, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.identitiesService.getIdentities().subscribe((identities) => {
      this.identitiesList = identities;
      this.filteredIdentitiesList = identities;
      this.changeDetectorRef.markForCheck();
    });
  }

  // Filtering by identity name value
  filterIdentities(event: KeyboardEvent) {
    const filter = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredIdentitiesList = this.identitiesList.filter((identity: IdentityDto) => identity.name.toLowerCase().includes(filter));
  }
}
