import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { combineLatest, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { TransactionDto } from '../../transactions/transaction-dto';
import { WalletExtendedDto } from '../wallet-extended-dto';
import { WalletPolicyDto } from '../wallet-policy-dto';
import { WalletsService } from '../wallets.service';

@Component({
  selector: 'tbtl-wallet',
  templateUrl: './wallet.component.html',
  styleUrls: ['./wallet.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class WalletComponent implements OnInit, OnDestroy {
  private unsubscribe$ = new Subject();

  wallet: WalletExtendedDto;
  walletPolicy: WalletPolicyDto;
  transactionList: TransactionDto[];
  filteredTransactionsList: TransactionDto[];

  constructor(private route: ActivatedRoute, private walletsService: WalletsService, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.loadData();
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  loadData() {
    const walletId = this.route.snapshot.params.walletId;

    combineLatest([
      this.walletsService.getWalletById(+walletId),
      this.walletsService.getWalletPolicyById(+walletId),
      this.walletsService.getTransactionsByWalletId(+walletId),
    ])
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(([wallet, walletPolicy, transactionList]: [WalletExtendedDto, WalletPolicyDto, TransactionDto[]]) => {
        this.wallet = wallet;
        this.walletPolicy = walletPolicy;
        this.transactionList = transactionList;
        this.filteredTransactionsList = transactionList;
        this.changeDetectorRef.markForCheck();
      });
  }

  // Filtering by transaction Hash value
  filterTransactions(event: KeyboardEvent) {
    const filter = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredTransactionsList = this.transactionList.filter((trans: TransactionDto) => trans.hash.toLowerCase().includes(filter));
  }
}
